import * as Types from '../types';
import API from '../../default';
import { getLoginSalt } from '../../hash';

export const RecoverPassword = email => {
    const hash = getLoginSalt(email,"");

    const sendData = {
                "Email": email,
                "Pass": "password1",
                "confirmpass": "password1",
                "Hashpass": hash
            }
    return dispatch => {
        dispatch({
          type: Types.PASSWORD_RECOVERY,
          payload: API({
            method: 'POST',
            url: `/?end=Resetpassword`,
            data: sendData
          })
        })
    }
}
