import React, {Component} from 'react';
import {connect} from 'react-redux';

import { Container,Row,Box,Image, Col, Column, Button} from '../../styles/init';
import Navigation from '../../reuseables/navigation';
import Footer from '../../reuseables/footer';
import Form from '../../reuseables/form';
import {RecoverPassword} from '../../redux/actions/passwordRecovery';

class PasswordRecovery extends Component {
  constructor(props) {
    super(props)
    this.state = {
      email:"",
      isempty: true
    }
  }

  getEmailData = (value,valid) => { 
    this.setState({
      email: value 
    });
  }

  resetMail = () => {
    if( !(this.state.email === "")){
      this.props.RecoverPassword(this.state.email);
    }else{
      this.setState({isempty: false})
      setTimeout(()=> this.setState({isempty: true}), 5000);
    } 
  }
  render() {
    const {loading, payload} = this.props;
    return (
      <Container>
        <Column height="100vh" justify="space-between">
          <Navigation />
          <Column height="350px">
            <Row bottomspacing="20px" justify="center">
              <h1>Forgot your password?</h1>
            </Row>
            <Row bottomspacing="50px" justify="center">
              <p>please enter the email address registered on your account</p>
            </Row>
            <Row bottomspacing="17px" justify="center">
              <p>{payload? payload.Message : ''}</p>
            </Row>      
            <Row bottomspacing="20px" justify="center">
              <Form
                  width="450px"
                  placeholder="Enter Your Email"
                  label="Email Address"
                  type="email"
                  data={this.getEmailData}
              />
            </Row>
            <p style={{color:"red",fontSize:"10px",transform:"translateY(-15px)",width:"450px"}}>{!this.state.isempty? "you have to fill the input field" : null  }</p>
            <Button width="450px" height="58px" onClick={this.resetMail}>{loading ? <div className="loader"></div> : `Reset password`}</Button>
          </Column>
          <Footer/>
        </Column>
      </Container>

    )
  }
}

const mapStateToProps = (state) => {
  return {
    payload: state.Passwordrecovery.payload,
    loading: state.Passwordrecovery.loading
  }
}

export default connect(mapStateToProps,{RecoverPassword})(PasswordRecovery);