import React, { Component } from 'react';
import { Row,Col } from '../../styles/init';
import {DefaultNavs,SearchNavs,AuthNavs, AuthLogout} from './navs';
import {NavLink} from 'react-router-dom';

export default class Navigation extends Component {
    render() {
        return (
            <Row height="96px" padding="0px 86px" justify="space-between">
                <Col width="20%">
                    <NavLink to="/"><img className="navimg" src={require('../../assets/icon.svg')} alt="icon"/></NavLink>        
                </Col>
                <Col width="60%">
                    <Row justify="space-between" align="center">
                        {DefaultNavs.map((nav,index)=>{
                            return (
                                <NavLink key={index} to={nav.path} activeClassName="active" className="navlist">
                                    {nav.name}
                                </NavLink>
                            )
                        })}
                        { localStorage.getItem("ax_auth") ? AuthLogout.map((nav,  index)=>{
                            return (
                                <NavLink key={index} to={nav.path} activeClassName="active" className="navlist">
                                    {nav.name}
                                </NavLink>
                            )
                        }) : null }
                        { localStorage.getItem("ax_auth") ? null : AuthNavs.map((nav,  index)=>{
                            return (
                                <NavLink key={index} to={nav.path} activeClassName="active" className="navlist">
                                    {nav.name}
                                </NavLink>
                            )
                        })  }
                        {SearchNavs.map((nav,index)=>{
                            return (
                                <NavLink key={index} to={nav.path} activeClassName="active" className="navlist">
                                    <img src={nav.icon} alt={nav.name} />
                                </NavLink>
                            )
                        })}
                    </Row>
                </Col>
            </Row>
        )
    }
}
